import React, { useState } from "react";
import { TextField } from "@mui/material";

const RaciosCalculateComponent = ({ label, onChange, value }) => {
  const [inputValue, setInputValue] = useState(value.toString().replace(".", ",")); // Converte nmero para string

  const handleChange = (e) => {
    let rawValue = e.target.value.replace(/[^0-9,]/g, ""); // Permite apenas nmeros e vrgula
    setInputValue(rawValue);
    
    const numericValue = parseFloat(rawValue.replace(",", ".")) || 0; // Converte para nmero
    onChange(numericValue);
  };

  return (
    <TextField
      label={label}
      variant="filled"
      fullWidth
      size="small"
      value={inputValue}
      onChange={handleChange}
    />
  );
};

export default RaciosCalculateComponent;
